import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { CustomTable } from '../../reuseableComponents'
import { Icon } from '../../commanComponent'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction, locationActions, travelAndClaimAction } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import ViewTravelDetail from './ViewTravelDetail'
import { useCustomTranslation } from '../../../translation'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import ProgressModel from '../../leave_management/ProgressModel'
import { isPermission } from '../../../utils/common'

const Trip = () => {
  const { t } = useCustomTranslation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [travelRequestData, setTravelRequestData] = useState('')
  const {
    getTravelRequestPending,
    getTravelRequestRes,
    getTravelRequestError,

    deleteTravelPending,
    deleteTravelRes,
    deleteTravelError,

  } = useSelector(state => state.travelAndClaim)
  const {
    getLeaveProgressPending,
    getLeaveProgressRes,
    getLeaveProgressError,
  } = useSelector(state => state.leave);
  const updateTravelPer =isPermission("update_travel")
  const deleteTravelPer =isPermission("delete_travel")
  const viewAppTravelStatusPer =isPermission("view_approval_travel_status")
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedTravelData, setSelectedTravelData] = useState('')
  const [isOpenTravelDetailModal, setIsOpenTravelDetailModal] = useState(false)
  const [isShowDeleteTravelModal, setIsShowDeleteTravelModal] = useState(false)
  const [deleteTravelId, setDeleteTravelId] = useState('')
  const [openProgressModel, setOpenProgressModel] = useState(false)
  const [progressData, setProgressData] = useState('')

  
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'name', label: 'Name' },
    { key: 'travel_type', label: 'Travel Type' },
    { key: 'purpose_of_travel', label: 'Business Purpose' },
    { key: 'travel_detail', label: 'Travel Detail' },
    ((updateTravelPer ||deleteTravelPer ||viewAppTravelStatusPer) &&{ key: 'action', label: 'Action' })
  ]
  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    travel_detail: item => (
      <span onClick={() => toggleTravelDetail(item)}>
        <img src="images/view-icon.svg" alt width="32" />
      </span>
    ),

    action: item => (
      <div className="btn-group actionDrp">
        <button type="button" className="btn dropdown-toggle-split"
          data-bs-toggle="dropdown" aria-expanded="false">
          <i className="bi bi-three-dots-vertical"></i>
        </button>
        <ul className="dropdown-menu">
         {viewAppTravelStatusPer && <li>
            <span className="dropdown-item"
              onClick={() => {
                onHandleProgressModel("travel", item?._id)
              }}
            >
              <Icon className={"fa fa-check me-1"} />Approval Status</span>
          </li>}
          {updateTravelPer&&<li>
            <span className="dropdown-item"
              onClick={() => {
                addUpdateTrip(item);
              }}
            >
              <Icon className={"fa fa-edit me-1"} />Update</span>
          </li>}
          {deleteTravelPer &&<li>
            <span className="dropdown-item"
              onClick={() => handleTravelDeleteModal(item._id)}
            >
              <Icon className={"bi bi-trash me-1"} />Delete</span>
          </li>}
        </ul>
      </div>

    )
  }), []);
  const addUpdateTrip = (item) => {
    dispatch(travelAndClaimAction.selectedTravelData(item))
    navigate('/add-update-travel')
  }

  const toggleTravelDetail = (data) => {
    setIsOpenTravelDetailModal(!isOpenTravelDetailModal)
    setSelectedTravelData(data)
  }

  useEffect(() => {
    getTravelRequestData()
  }, [currentPage])
  const getTravelRequestData = () => {
    const payload = {
      page: currentPage,
      limit: pagination.limit
    }
    dispatch(travelAndClaimAction.getTravelRequestRequest(payload));
  }

  useEffect(() => {
    if (!getTravelRequestPending) {
      if (getTravelRequestError) {
        return;
      }
      if (getTravelRequestRes) {
        if (getTravelRequestRes?.code === 200) {
          setTravelRequestData(getTravelRequestRes?.data)

        }
      }
    }
  }, [
    getTravelRequestPending,
    getTravelRequestRes,
    getTravelRequestError,
  ]);

  /*  Delete Travel   */
  const handleTravelDeleteModal = (id) => {
    if (id) {
      setDeleteTravelId(id)
    }
    setIsShowDeleteTravelModal(!isShowDeleteTravelModal)
  }

  const handleDeleteTravel = (type) => {
    if (type) {
      const params = {
        _id: deleteTravelId
      }
      dispatch(travelAndClaimAction?.deleteTravelRequest(params))

    } else {
      handleTravelDeleteModal()
    }
  }

  useEffect(() => {
    if (!deleteTravelPending) {
      if (deleteTravelError) {
        dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_DELETE_TRAVEL_TYPE_STORE"))
        return;
      }
      if (deleteTravelRes) {
        if (deleteTravelRes?.code === 200) {
          dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_DELETE_TRAVEL_TYPE_STORE"))
          handleTravelDeleteModal()
          getTravelRequestData()
        }
      }
    }
  }, [
    deleteTravelPending,
    deleteTravelRes,
    deleteTravelError,
  ]);

  /* approval process b*/
  const onHandleProgressModel = (type, id) => {
    if (type || id) {
      const payload = {
        id: id || '',
        type: type || ''
      }
      dispatch(leaveAction.getLeaveProgressRequest(payload))
    }
    setOpenProgressModel(!openProgressModel)
  }
  useEffect(() => {
    if (!getLeaveProgressPending) {
      if (getLeaveProgressError) {
        return;
      }
      if (getLeaveProgressRes) {
        if (getLeaveProgressRes?.code === 200) {
          setProgressData(getLeaveProgressRes?.data)
        }
      }
    }
  }, [
    getLeaveProgressRes,
    getLeaveProgressError,
    getLeaveProgressPending
  ]);

  return (
    <div className='cardBox myRequest mb-3 employeeHistory'>
      <div className="d-flex align-items-center justify-content-between gap-3">
        <div className="cardHead d-flex">
          <img src="images/myrequest-icon.svg" alt="" width={35} />
          <h4 className="ms-2 ">Travel</h4>
        </div>
        <CustomButton
          text={"New Travel"}
          classNames={"btn btn-primary"}
          icon={<Icon className="fa fa-plus" />}
          handleClick={() => addUpdateTrip()}
          slug={"add_travel"}
        />
      </div>
      <CustomTable
        data={travelRequestData?.data}
        fields={fields}
        isLoading={getTravelRequestPending}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={travelRequestData?.totalPages}
        isHeader={false}
        tableStyle={{ padding: "0px", boxShadow: "none" }}
        scopedSlots={scopedSlots}
      />
      {isOpenTravelDetailModal && <ViewTravelDetail
        openModel={isOpenTravelDetailModal}
        handleOpenClose={toggleTravelDetail}
        travelDetailData={selectedTravelData?.travel_details}
      />}
      {isShowDeleteTravelModal && (
        <ApproveRejectModal
          onApproveReject={handleDeleteTravel}
          isShow={isShowDeleteTravelModal}
          desc={t("alerts.deletePopup.desc").replace('$1', "Trip")}
          btnRightLoading={deleteTravelPending}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}

        />)
      }
      {openProgressModel && <ProgressModel
        openModel={openProgressModel}
        handleOpenClose={onHandleProgressModel}
        progressData={progressData}
        processType={"Travel & Claim"}
      />}
    </div>
  )
}
export default Trip
